<template>
  <div :class="{'multiple-img':true,'multiple-img--custom':isCustom}" :style="{marginTop: noMargin ? 0 : '0.24rem'}" v-if="data && data.images && data.images.length">
    <van-grid :border="false" :column-num="columnNum">
      <van-grid-item
        v-for="(item, index) in data.images"
        @click="imgClick(item,index)"
        :key="index">
        <van-image
          lazy-load
          :width='(isCustom ? (7.5/columnNum) : (7.02/columnNum))+ "rem"'
          :src="item.imgUrl.replace('http:', 'https:') | imageToWebp($webpSurpport)" />
      </van-grid-item>
    </van-grid>
  </div>
</template>
<script>
/** 头部tabbar 【精选】单列、双列、三列图 */
export default {
  props: {
    columnNum: {
      type: Number,
      default: 1,
    },
    data: {
      type: Object,
      default: () => {},
    },
    storeId: {
      type: String,
      default: '',
    },
    previousCode: {
      type: String,
      default: '',
    },
    isCustom: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
    };
  },
  computed: {
    noMargin() {
      return ['i_single', 'i_two', 'i_three', 'carousel', 'template_img'].includes(this.previousCode)
    }
  },
  methods: {
    imgClick(item, index) {
      const { urlType, urlParam } = item;
      // 25093埋点
      if (this.data.modelCode === 'draw' && Number(item.urlType) === 5 && urlParam) {
        this.$util.gioTrackWithVariable('mall_store_click_model', {
          modelName: '大转盘',
          modelCode: 'draw',
          site_number: index + 1 + '',
          itemCode: urlParam + '',
        })
      } else {
        switch (this.columnNum) {
          case 1:
            if (Number(urlType || '') === 1) {
              this.$util.gioTrackWithVariable('mall_store_click_model', {
                modelName: '自定义页面',
                modelCode: 'template_img',
                site_number: index + 1 + '',
                itemCode: 'null',
              })
            } else {
              this.$util.gioTrackWithVariable('mall_store_click_model', {
                modelName: '单列图',
                modelCode: 'i_single',
                site_number: index + 1 + '',
                itemCode: 'null',
              })
            }
            break;
          case 2:
            this.$util.gioTrackWithVariable('mall_store_click_model', {
              modelName: '双列图',
              modelCode: 'i_two',
              site_number: index + 1 + '',
              itemCode: 'null',
            })
            break;
          case 3:
            this.$util.gioTrackWithVariable('mall_store_click_model', {
              modelName: '三列图',
              modelCode: 'i_three',
              site_number: index + 1 + '',
              itemCode: 'null',
            })
            break;
        }
      }
      if (this.data.modelCode === 'draw') { // 如果是大转盘
        if (Number(item.urlType) === 5) {
          if (urlParam) {
            this.$router.push(`/games/storeLuckyWheel?gameId=${urlParam}&storeId=${this.storeId}`);
          }
        }
      } else {
        switch (Number(urlType || '')) { // 1自定义页面 2商品详情页 3领券中心 4海报页 5动态活动页面 6三翼鸟场景
          case 1: // 自定义页面
            if (urlParam) {
              this.$router.push(`/shopCustomPage?storeId=${this.storeId}&pageId=${urlParam}`);
            }
            break;
          case 2: // 商品详情页
            if (urlParam) {
              this.$router.push(`/goodsDetail?productId=${urlParam}&visitStore=${this.storeId}`);
            }
            break;
          case 3: // 领券中心
            this.$router.push(`/voucher?storeCouponid=${this.storeId}`);
            break;
          case 5: // 动态活动页
            if (urlParam) {
              this.$router.push(`/bannerCustom?id=${urlParam}`);
            }
            break;
          case 6: // 三翼鸟场景
            if (urlParam) {
              let url = urlParam + `&userId=${this.$store.state.ucId ? this.$store.state.ucId : ''}&userAccessToken=${this.$store.getters.getUserCenterToken}`
              this.$store.dispatch('goPageByType', {
                type: 5,
                params: this.$util.injectISOAddressCodeToURL(url, this.$store.getters.getlocationInfo)
              })
            }
            break;
        }
      }
    }
  },
}
</script>
<style lang="scss" scoped>
.multiple-img {
  width: 100%;
}
.single-img {
  width: 100%;
  object-fit: fill;
}
.van-grid ::v-deep .van-grid-item__content {
  padding: 0;
  margin:0;
  background: transparent;
}
.multiple-img--custom{
  width:7.5rem !important;
}
</style>
